import React, { Component } from "react";
import "./Experience.css";
import Card from "../Card/Card";
import Pdf1 from '../../Documents/SIGCSE_2021_submission_839.pdf';
import Pdf2 from '../../Documents/VTURCS_Research_Symposium.pdf';


class Experience extends Component {
  render() {
    const exp = [
      {
        title: "Graduate Teaching Assistant",
        place: "Virginia Tech",
        start: "Aug 2020",
        end: "Present",
        description: `路 Taught 600 CS2 level students, covering Data Structures and Algorithm and problem-solving skills \r
          路 Collaborated with 20 TAs on a weekly basis to develop a compact core Data Structures class \r
          路 Skills and Tools: Java, Unit Testing, Data Structures and Algorithms, Eclipse, Teaching, Communication`,
      },
      {
        title: "Graduate Research Assistant",
        place: "Virginia Tech",
        start: "Aug 2020",
        end: "Present",
        description: `路 Continued work from undergraduate research to enhance support for OpenDSA opensource
          路 Designed and Developed various ways to support data structures visualizations and exercises
          路 Collaborated with PhD students and professors to improve engagement from students with OpenDSA eTextbook
          路 Skills and Tools: JavaScript, HTML/CSS, JSON, Ruby, Rails, Docker, Linux, GitHub, Teamwork, Communication`,
        link: Pdf1,
      },
      {
        title: "Undergraduate Research Assistant",
        place: "Virginia Tech",
        start: "Dec 2018",
        end: "July 2020",
        description: `路 Designed and developed web application with React for start-up company to publish its first website
          路 Participated in all phases of the development lifecycle, beginning with the gathering of requirements, and project specifications
          路 Managed project tasks and timelines and communicate project status and deliverables with team
          路 Skills and Tools: ReactJS, Redux, Ajax, HTML/CSS, JSON, GitHub, Teamwork, Communication`,
        link: Pdf2,
      },
      {
        title: "Machine Learning Competition",
        place: "Algorima",
        start: "July 2019",
        end: "Aug 2019",
        description: `路 Participated in Machine Learning competition hosted by the government of South Korea with 4 teammates
        路 Took 2nd place in Movie Review Score Prediction among 70 teams and 3rd place in Intent Classification
        路 Implemented different models including RNN with Bi-LSTM model and basic CNN model
        路 Skills and Tools: Pytorch, TensorFlow, Keras, Kaggle, GitHub, Adaptability, Teamwork, Communication`,
      },
      {
        title: "Software Developer Internship",
        place: "Algorima",
        start: "June 2019",
        end: "Aug 2019",
        description: `路 Designed and developed web application with React for start-up company to publish its first website
          路 Participated in all phases of the development lifecycle, beginning with the gathering of requirements, and project specifications
          路 Managed project tasks and timelines and communicate project status and deliverables with team
          路 Skills and Tools: ReactJS, Redux, Ajax, HTML/CSS, JSON, GitHub, Teamwork, Communication`,
      },
    ];

    const expList = exp.map((data) => {
      return (
        <Card
          title={data.title}
          place={data.place}
          start={data.start}
          end={data.end}
          desc={data.description}
          link={data.link ? data.link : null}
        />
      );
    });

    return (
      <div id="experience" className="experience">
        <h1>Experience</h1>
        {expList}
      </div>
    );
  }
}

export default Experience;
